.header-mobile__delivery {
  padding: 20px 10px; }
  .header-mobile__delivery * {
    box-sizing: border-box; }
  .header-mobile__delivery__icon {
    width: 36px;
    margin: 0 auto 15px auto; }
    .header-mobile__delivery__icon img {
      width: 100%; }
  .header-mobile__delivery__how {
    display: flex;
    margin: 0 -5px; }
    .header-mobile__delivery__how__col {
      flex: 0 0 auto;
      width: 50%;
      display: flex;
      flex-direction: column;
      padding: 0 5px;
      min-height: 70px; }
    .header-mobile__delivery__how__item {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      flex-grow: 1;
      padding: 15px;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
      background: #fff;
      font-size: 11px;
      line-height: 1.2; }
      .header-mobile__delivery__how__item.active {
        box-shadow: 0 0 7px rgba(0, 0, 0, 0.2);
        color: #ff47a5; }

.header-mobile__catalog {
  padding: 19px 10px;
  border: 1px solid #000;
  border-width: 1px 0; }

.header-mobile__topbar {
  display: flex;
  align-items: center;
  padding: 7px 10px; }
  .header-mobile__topbar__logo {
    flex: 0 0 auto;
    width: 50px;
    margin-right: 25px; }
    .header-mobile__topbar__logo img {
      display: block; }
  .header-mobile__topbar__icons {
    display: flex;
    align-items: center;
    margin-left: auto; }
    .header-mobile__topbar__icons__item {
      position: relative;
      margin-left: 30px; }
      .header-mobile__topbar__icons__item svg {
        transition: all .2s;
        width: 17px;
        height: 17px; }
      .header-mobile__topbar__icons__item:hover svg, .header-mobile__topbar__icons__item.active svg {
        fill: #ff47a5; }
    .header-mobile__topbar__icons__count {
      position: absolute;
      top: -4px;
      right: 100%;
      margin-right: -4px;
      padding: 2px 5px;
      border-radius: 10px;
      background: #25aabf;
      font-size: 9px;
      line-height: 1;
      color: #fff; }

.header-desktop__slogan {
  font-family: "CyrillicOld";
  font-size: 43px;
  font-weight: normal;
  line-height: 1;
  color: #e30413; }

.header-desktop__delivery {
  display: flex;
  align-items: center; }
  .header-desktop__delivery__icon {
    flex: 0 0 auto;
    width: 30px;
    margin-right: 10px; }
    .header-desktop__delivery__icon img {
      width: 100%; }
  .header-desktop__delivery__how__item {
    font-size: 14px;
    text-decoration: underline;
    color: #ff47a5 !important; }
